*,*::after,*::before{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: 'Roboto Slab', serif;
  color: #222;
}

img{
  max-width: 100%;
  filter: grayscale(1);
}

a{
  text-decoration: none;
  color: #222;
}

ul{
  list-style: none;
}

section{
  padding-top: 30px;
  padding-bottom: 30px;
}

.copyright{
  color: #777777;
  font-size: 14px;
  text-transform: none;
  font-weight: 300;
  line-height: 20px;
  margin-top: 100px;
  text-align: center;
}

.strong{
  font-weight: 700;
  color: #222;
}

.grid-2{
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 50px;
}

.slick-active.slick-current{
  padding-left: 50px;
  padding-right: 50px;
}

.slick-arrow{
  width: 50px !important;
  height: 50px !important;
  z-index: 55555;
  position: relative;
  border-radius: 50%;
}

.slick-arrow::after{
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  padding: 20px;
  border-radius: 50%;
  border: solid 2px black;
  opacity: 0.5;
  transform: rotate(-45deg);
  transition: 0.3s;
}

.slick-arrow:hover::after{
  opacity: 1;
}

.slick-arrow::before{
  font-size: 30px !important;
  background-color: transparent !important;
  color: black !important;
}

.slick-prev {
  left: -80px !important;
}

.slick-prev::after{
  border-left-color: transparent;
  border-top-color: transparent;
}

.slick-next{
  right: -80px !important;
}

.slick-next::after{
  border-bottom-color: transparent;
  border-right-color: transparent;
}

@media ( max-width: 992px ){
  .md2-sm1{
      grid-template-columns: 1fr 1fr !important;
  }
}

@media ( max-width: 760px ){
  .md2-sm1{
      grid-template-columns: 1fr !important;
  }

  .grid-2{
    grid-template-columns: 1fr;
  }
}
