.teams{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 30px;
}

.teams .team{ 
    height: 425px;
    position: relative;
    cursor: pointer;
    overflow: hidden;
}

.feature-img{
    height: 100%;
    background-image: linear-gradient( 90deg, rgba(0, 0, 0, 0.055) 50%, white 100% ) ;
}

.feature-img img{
    width: 100%;
    filter: grayscale(1);
    height: 100%;
    object-fit: cover;
}

.team-body{
    padding: 50px 40px;
    position: absolute;
    top: 0;
    left: 100%;
    right: 0;
    bottom: 0;
    background: rgba(255,255,255,0.9);
    transition: all 0.3s linear;
}

.teams .team:hover .team-body{
    left: 0;
}

.title{
    font-size: 18px;
    margin-bottom: 10px;
    font-weight: 700;
    text-transform: uppercase;
}

.designation{
    font-weight: 300;
    color: #555;
    font-size: 14px;
}

.short-bio{
    padding: 35px 0px 0px 0px;
    margin-bottom: 15px;
    line-height: 26px;
    color: #666;
    font-weight: 200;
    font-size: 17px;
    opacity: 0;
    height: 0;
}

.teams .team:hover .short-bio{
    transition: all 0.5s linear;
    height: auto;
    opacity: 1;
}

.social-links{
    display: flex;
    list-style: none;
    column-gap: 20px;
}

.social-link{
    font-size: 0px;
}

.social-icon i{
    color: #555;
    font-size: 22px;
}