.project{
    position: relative;
    height: 400px;
}

.project-body{
    position: absolute;
    top: 0;
    left: 100px;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    padding: 50px;
}

.feature{
    width: 100%;
    height: 100%;
    position: relative;
}

.feature::after{
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    display: block;
    background-color: rgba( 0, 0, 0, 0.3 );
    width: 100%;
    height: 100%;
}

.feature img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: 111;
}

.project-title{
    text-transform: uppercase;
    letter-spacing: 0.2em;
    font-size: 1rem;
    color: #fff;
}

.title{
    font-size: 4rem;
    margin-bottom: 0.5rem;
    font-weight: 700;
    color: #fff;
}

.description{
    color: #fff;
    font-size: 1.5rem;
}

