.hero{
    width: 100%;
    height: 80vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
}

.hero-title{
    margin-top: 0;
    margin-bottom: 23px;
    font-weight: 700;
    font-size: 47px;
    line-height: 1.3;
    letter-spacing: 0.5px;
    max-width: 600px;
}

.hero-subtitle{
    line-height: 26px;
    color: #666;
    font-weight: 200;
    font-size: 17px;
}

.hero-btn{
    border-radius: 25px;
    border: solid 1px #999;
    height: 45px;
    width: 200px;
    background-color: #fff;
    cursor: pointer;
    margin-top: 30px;
    font-size: 14px;
}

.hero-btn:hover{
    border: solid 1px black;;
}