.container{
  margin-left: auto;
  margin-right: auto;
  padding-left: 10px;
  padding-right: 10px;
}

@media ( min-width: 576px ){
  .container{
    width: 540px;
  }
}

@media ( min-width: 768px ){
  .container{
    width: 750px;
  }
}

@media ( min-width: 992px ){
  .container{
    width: 970px;
  }
}

@media ( min-width: 1200px ){
  .container{
    width: 1170px;
  }
}

@media ( max-width: 576px ){
  .container{
    width: 100%;
  }
}