.title{
    font-size: 20px;
    line-height: 28px;
    font-weight: 700;
    text-transform: uppercase;
    padding-bottom: 15px;
}

.description{
    line-height: 26px;
    color: #666;
    font-weight: 200;
    font-size: 17px;
}

.sub-title{
    margin-top: 30px;
    font-size: 18px;
    line-height: 25px;
    text-transform: uppercase;
}

.tools{
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
}

.tool img{
    width: 50px;
    height: 50px;
    filter: grayscale(1);
}

.right{
    width: 100%;
    overflow: hidden;
}

.right img{
    cursor: pointer;
}

.right img:hover{
    transform: scale(1.2);
    transition: all 0.2s linear;
}