.services{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 30px;
}

.title{
    padding: 15px 0px 25px;
    font-weight: 700;
    font-size: 18px;
    line-height: 22px;
}

.description{
    line-height: 26px;
    color: #666;
    font-weight: 200;
    font-size: 17px;
}