.form-title{
    font-size: 18px;
    line-height: 22px;
    font-weight: 700;
    text-transform: uppercase;
    margin-bottom: 30px;
}

.input{
    width: 100%;
    height: 45px;
    border: 1px solid #999;
    border-radius: 0;
    margin-top: 10px;
    margin-bottom: 10px;
    padding: 10px 20px;
}

.input:focus{
    outline: 0;
    border-color: black;
}

.textarea{
    height: 250px;
    padding-top: 20px;
    padding-bottom: 20px;
}

.form-btn{
    background-color: black;
    width: 200px;
    border-radius: 0;
    border: 0;
    height: 45px;
    color: #fff;
    font-size: 14px;
    line-height: 22px;
}

.contact-details{
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
}

.list{
    margin-bottom: 20px;
}

.title{
    font-size: 14px;
    line-height: 22px;
    font-weight: 300;
    text-transform: uppercase;
    font-weight: 700;
}

.description{
    line-height: 26px;
    color: #666;
    font-weight: 200;
    font-size: 17px;
}